.button-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #303030;
    height: 50px;
    width: 100%;
    border-top: 1px solid #202020;
    padding: 0 20px;
  }
  
  .button {
    background-color: #1d1d1d;
    border: 3px solid blue;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    margin: 0 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;
  }
  
  .button:hover {
    background-color: #555;
  }