.page {
    display: flex;
    flex-direction: column;
    height: 100vh; 
    width: 100vw;
  }

.pageHolder {
    flex: 1;
    overflow: auto;
    box-sizing: border-box;  
    background-color: #303030;
    text-align: center;
    color: #fff;
    border: 3px solid #ff0000a9;
    width: 100%;
    overflow-x: hidden;
    overflow-y: auto; 
  }
  
  .chart {
    grid-row: 1; 
    display: flex;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.034);
    border: 3px solid #000000;
    overflow-x: hidden;
    overflow-y: hidden; 
  }
  
  .player-cards {
    overflow: auto;
    grid-row: 2;
    background-color: rgba(0, 0, 0, 0);
    display: flex;
    grid-template-columns: repeat(auto-fill, minmax(150px, 2fr)); 
    gap: 10px; 
    justify-content: flex-start;
    padding: 10px;
    overflow-x: hidden;
    overflow-y: hidden; 
    align-items: center;
  }
  
  .buttonHolder {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    background-color: #303030;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    padding: 15px;
    height: 10px;
    width: 100%;
    border: 5px solid #202020;
    overflow-x: hidden;
    overflow-y: hidden; 
  }
  