.page {
    display: flex;
    flex-direction: column;
    background-color: blue;
    height: 100vh; 
    width: 100vw;
  }

  .pageHolder {
    flex: 1;
    overflow: auto;
    box-sizing: border-box;  
    background-color: #303030;
    text-align: center;
    color: #fff;
    border: 3px solid #ff0000a9;
    width: 100%;
    overflow-x: hidden;
    overflow-y: auto; 
  }