.charts-selection {
    display: flex;
    background-color: rgb(87, 87, 87);
    border: 10px solid #252525;
    height: 500px;
    width: 900px;
    padding: 10px;
  }

  .left-area {
    background-color: rgb(36, 36, 36);
    padding: 10px;
    border: 3px solid rgb(5, 5, 5);
    display: flex;
    flex-direction: column;
    flex: 1; 
  }
  
  .chart-selector {
    display: flex;
    flex-direction: column;
    
  }
  
  .chart-selector label {
    font-weight: bold;
  }
  
  .right-chart {
    flex: 2; 
    margin-left: 10px; 
  }
  
  .chart-selector select {
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .right-chart {
    box-sizing: border-box;
    border: 10px solid #252525;
    width: 100%;
    max-width: 500px; 
    height: auto;
  }

  .stat-options-area {
    display: flex;
    background-color: rgba(56, 56, 56, 0.384);
    border: 3px solid rgb(2, 80, 26)
  }
  
  .main-stat-options {
    flex: 1;
    background-color: rgba(94, 94, 94, 0.384);
    border: 3px solid rgb(41, 41, 41);
    width: 150px;
    flex-direction: column;
  }

  .alt-stat-options {
    flex: 1;
    background-color: rgba(94, 94, 94, 0.384);
    border: 3px solid rgb(41, 41, 41);
    width: 150px;
    flex-direction: column;
  }
