html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background: #000000;
  color: #fff;
  height: 100%;
  display: flex; 
  align-items: center;
  justify-content: center; 
}

.page {
  display: flex;
  flex-direction: column;
  height: 100vh; 
  width: 100vw;
  overflow-x: hidden;
  overflow-y: hidden; 
}

.logo {
  padding: 10px;
}

.buttonHolder {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  background-color: #303030;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 15px;
  height: 10px;
  width: 100%;
  border: 5px solid #202020;
  overflow-x: hidden;
  overflow-y: hidden; 
}

